export function replacePlaceholdersInConfig(
  config: Record<string, unknown>,
  inputs: Record<string, unknown>,
): Record<string, unknown> {
  const placeholderRegex = /{{message\.(\w+)}}/;

  const replacePlaceholders = (value: unknown): unknown => {
    if (typeof value === 'string') {
      const match = value.match(placeholderRegex);
      if (match) {
        const messageKey = match[1];
        return inputs[messageKey] !== undefined ? inputs[messageKey] : value;
      }
      return value;
    }

    if (Array.isArray(value)) {
      return value.map(replacePlaceholders);
    }

    if (typeof value === 'object' && value !== null) {
      return Object.fromEntries(
        Object.entries(value).map(([key, val]) => [
          key,
          replacePlaceholders(val),
        ]),
      );
    }

    return value;
  };

  return replacePlaceholders(config) as Record<string, unknown>;
}
